<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
      @openDialog="openDialog"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      @sort-change="sortChange"
    >
      <el-table-column
        label="操作"
        width="60"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'operate')"
            type="text"
            size="small"
          >
            处理
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="contractNo"
        label="合同编号"
        sortable
        width="100"
      >
      </el-table-column>
      <el-table-column
        prop="wareHouseId"
        label="仓单编号(仓单总数)"
        :formatter="wwIdFormatter"
        width="180%"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'wareHouse')"
            type="text"
            size="small"
          >
            {{ scope.row.warehouseWarrantList.length > 1 ? scope.row.warehouseWarrantList[0].wwNo+"~"+ scope.row.warehouseWarrantList[scope.row.warehouseWarrantList.length-1].wwNo+"("+scope.row.warehouseWarrantList.length+")":((scope.row.warehouseWarrantList.length==1)?(scope.row.warehouseWarrantList[0].wwNo)+"(1)":"(0)")}}
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="status"
        label="仓单状态"
        sortable
        :formatter="statusSwitch"
        width="100"
      >
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="交易客户"
        sortable
        width="100"
      >
      </el-table-column>
      <el-table-column
        prop="goodsName"
        label="品种"
        sortable
        width="70"
      >
      </el-table-column>
      <el-table-column
        prop="contractFileList"
        label="出账记录"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'account')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="仓单总价值/总现价"
        :formatter="priceFormatter"
        sortable
        width="145"
      >
      </el-table-column>
      <el-table-column
        prop="deposit"
        label="保证金"
        sortable
      >
      </el-table-column>
      <el-table-column label="保值方案">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'method')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="totalLoan"
        label="对应放款金额"
        sortable
        width="120"
      >
      </el-table-column>
      <el-table-column
        label="当前担保率"
        sortable
        width="110"
      >
        <template slot-scope="scope">
          {{pledgeRateFormatter(scope.row)}}
        </template>
      </el-table-column>
      <el-table-column
        prop="tradeEndtime"
        label="仓单交易到期日"
        sortable
        width="130"
      >
        <template slot-scope="scope">
          {{dateFormat(scope.row.tradeEndtime)}}
        </template>
      </el-table-column>
      <!-- <el-table-column
        prop="loanEndtime"
        label="贷款到期日"
        sortable
        width="110"
      >
        <template slot-scope="scope">
          {{dateFormat(scope.row.loanEndtime)}}
        </template>
      </el-table-column> -->
      <el-table-column
        prop="warehouseWarrantList"
        label="监控及影像信息"
        width="110"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'monitor')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
    <handleDialog
      :dialogFormVisible="dialogFormVisible"
      :form="form"
      :isDelete="true"
      @searchInfo="searchInfo"
      @dialogFormVisibleChange="dialogFormVisibleChange"
      @deleteRecord="deleteRecord"
    >
    </handleDialog>
    <el-dialog
      title="添加合同"
      :visible.sync="addContractVisible"
      width="1200px"
    >
      <el-form
        :model="contractForm"
        :inline="true"
        ref="contractForm"
        :rules="rules"
      >
        <el-form-item
          label="客户名称"
          :label-width="formLabelWidth"
          prop="customerName"
        >
          <el-input v-model="contractForm.customerName"></el-input>
        </el-form-item>
        <el-form-item
          label="客户号"
          :label-width="formLabelWidth"
          prop="customerNo"
        >
          <el-input v-model="contractForm.customerNo"></el-input>
        </el-form-item>
        <el-form-item
          label="仓单品种"
          :label-width="formLabelWidth"
          prop="goodsName"
        >
          <el-input v-model="contractForm.goodsName"></el-input>
        </el-form-item>
        <el-form-item
          label="仓单原价值"
          :label-width="formLabelWidth"
          prop="originalPrice"
        >
          <el-input v-model="contractForm.originalPrice"></el-input>
        </el-form-item>
        <el-form-item
          label="合同编号"
          :label-width="formLabelWidth"
          prop="contractNo"
        >
          <el-input v-model="contractForm.contractNo"></el-input>
        </el-form-item>
        <el-form-item
          label="联系人"
          :label-width="formLabelWidth"
          prop="customerLinkman"
        >
          <el-input v-model="contractForm.customerLinkman"></el-input>
        </el-form-item>
        <el-form-item
          label="联系电话"
          :label-width="formLabelWidth"
          prop="customerPhone"
        >
          <el-input v-model="contractForm.customerPhone"></el-input>
        </el-form-item>
        <el-form-item
          label="交货时间"
          :label-width="formLabelWidth"
          prop="deliveryTime"
        >
          <el-date-picker
            type="datetime"
            placeholder="选择交货时间"
            v-model="contractForm.deliveryTime"
            value-format="timestamp"
            style="width:200px"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="放款金额"
          :label-width="formLabelWidth"
          prop="totalLoan"
        >
          <el-input v-model="contractForm.totalLoan"></el-input>
        </el-form-item>
        <el-form-item
          label="合同交易到期日"
          :label-width="formLabelWidth"
          prop="tradeEndtime"
        >
          <el-date-picker
            type="datetime"
            placeholder="选择合同交易到期日"
            v-model="contractForm.tradeEndtime"
            value-format="timestamp"
            style="width:200px"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="交货地点"
          :label-width="formLabelWidth"
          prop="deliveryLocation"
        >
          <el-input v-model="contractForm.deliveryLocation"></el-input>
        </el-form-item>
        <el-form-item
          label="追保率"
          :label-width="formLabelWidth"
          prop="recoveryRate"
        >
          <el-input
            v-model="contractForm.recoveryRate"
            style="width:70px"
          ></el-input>%
        </el-form-item>
      </el-form>
      <span class="dialog-title">仓单信息</span>
      <div class="tableDate">
        <div
          class="button"
          style="width:3%;float:right;"
        >
          <P>
            <el-button
              class="el-icon-plus"
              @click.prevent="addRow()"
            ></el-button>
          </P>
          <p>
            <el-button
              class="el-icon-minus"
              @click.prevent="delData()"
            ></el-button>
          </p>
        </div>
        <div class="table">
          <el-table
            :data="wareHouseTable"
            ref="wareHouseTable"
            tooltip-effect="dark"
            border
            stripe
            style="width: 95%"
            @selection-change="selectRow"
          >
            <el-table-column
              type="selection"
              width="45"
              align="center"
            ></el-table-column>
            <el-table-column
              label="仓单编号"
              align="center"
              width="195"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.wwNo"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="商品名称"
              width="90"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsName"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="品牌/牌号"
              width="100"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.brand"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="质量标准"
              width="110"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.qualityStandard"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="包装规格"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.packingSpecification"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="数量"
              width="105"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.quantity"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="计量单位"
              width="80"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.unit"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="仓库名称"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.warehouseName"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="仓库地址"
              width="205"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.warehouseAddress"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="联系人"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.linkman"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="联系电话"
              width="165"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.linkPhone"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="货物位置"
              width="165"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsAddress"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label="货物真实性"
              width="115"
            >
              <template slot-scope="scope">
                <el-input v-model="scope.row.goodsAuthenticity"></el-input>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="addContractVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="addContract"
        >保 存</el-button>
      </div>
    </el-dialog>

    <el-dialog
      title="仓单编号："
      :visible.sync="warehouseVisible"
      width="700px"
    >
      <div class="head-tool">
        <div class="warehouseNum">{{wareHouseInfo.wwNo}}</div>
        <el-autocomplete
          class="selectSearch"
          v-model="state"
          :fetch-suggestions="querySearchAsync"
          placeholder="请输入内容"
          @select="handleSelect"
        ></el-autocomplete>
        <el-button
          class="addWarehouse"
          type="primary"
          @click="deleteRecord('仓单')"
        >删除该仓单</el-button>
        <el-button
          class="addWarehouse"
          type="primary"
          @click="warehouseInnerVisible = true"
        >追加仓单</el-button>
      </div>
      <div class="dialog-info">
        <span class="dialog-name">交易客户：</span>
        <span class="dialog-company">{{wareHouseInfoCurrent.customerName}}</span>
      </div>
      <el-form
        :model="wareHouseInfo"
        style="margin-left:10px"
        :rules="rules"
      >
        <el-form-item
          label="商品种类"
          :label-width="formLabelWidth"
        >
          {{wareHouseInfo.goodsName}}
        </el-form-item>
        <el-form-item
          label="商品名称"
          :label-width="formLabelWidth"
        >
          {{wareHouseInfo.goodsName}}
        </el-form-item>
        <el-form-item
          label="品牌/牌号"
          :label-width="formLabelWidth"
        >
          {{wareHouseInfo.brand}}
        </el-form-item>
        <el-form-item
          label="质量标准"
          :label-width="formLabelWidth"
        >
          {{wareHouseInfo.qualityStandard}}
        </el-form-item>
        <el-form-item
          label="包装规格"
          :label-width="formLabelWidth"
        >
          {{wareHouseInfo.packingSpecification}}
        </el-form-item>
        <el-form-item
          label="数量"
          :label-width="formLabelWidth"
        >
          {{wareHouseInfo.quantity}}
        </el-form-item>
        <el-form-item
          label="计量单位"
          :label-width="formLabelWidth"
        >
          {{wareHouseInfo.unit}}
        </el-form-item>
      </el-form>
      <el-dialog
        title="追加仓单："
        :visible.sync="warehouseInnerVisible"
        width="1200px"
        append-to-body
      >
        <div class="tableDate">
          <div
            class="button"
            style="width:3%;float:right;"
          >
            <P>
              <el-button
                class="el-icon-plus"
                @click.prevent="addRow1()"
              ></el-button>
            </P>
            <p>
              <el-button
                class="el-icon-minus"
                @click.prevent="delData1()"
              ></el-button>
            </p>
          </div>
          <div class="table">
            <el-table
              :data="addWareHouseTable"
              ref="addWareHouseTable"
              tooltip-effect="dark"
              border
              stripe
              style="width: 95%"
              @selection-change="selectRow1"
            >
              <el-table-column
                type="selection"
                width="45"
                align="center"
              ></el-table-column>
              <el-table-column
                label="仓单编号"
                align="center"
                width="195"
              >
                <template slot-scope="scope">
                  <el-input v-model="scope.row.wwNo"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="商品名称" width="90">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.goodsName"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="品牌/牌号" width="100">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.brand"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="质量标准" width="110">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.qualityStandard"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="包装规格" width="115">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.packingSpecification"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="数量" width="105">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.quantity"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="计量单位" width="80">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.unit"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="仓库名称" width="115">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.warehouseName"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="仓库地址" width="205">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.warehouseAddress"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="联系人" width="115">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.linkman"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="联系电话" width="165">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.linkPhone"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="货物位置" width="165">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.goodsAddress"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="货物真实性" width="115">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.goodsAuthenticity"></el-input>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div
          slot="footer"
          class="dialog-footer"
        >
          <el-button type="primary" @click="submitWarehouse">录 入</el-button>
          <!-- <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button> -->
        </div>
      </el-dialog>
    </el-dialog>
    <el-dialog
      title="出账记录"
      :visible.sync="accountRecordVisible"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="支付回单">
          <div class="recordInfo">合同编号：{{recordList.contractNo}}</div>
          <div class="recordInfo">交易客户：{{recordList.customerName}}</div>
          <div>
            <el-carousel
              height="350px"
              :autoplay="false"
              ref="payCarousel"
            >
              <el-carousel-item
                v-for="item in recordList.fileTypeZero"
                :key="item.index"
              >
                <img
                  :src="'http://192.168.2.86:8087' + item.filePath"
                  class="bannerImg"
                  style="width: 880px; height:350px; max-width: 100%; max-height: 100%; text-align: center;"
                />
              </el-carousel-item>
            </el-carousel>
            <div class="buttonGroup">
              <el-button
                type="primary"
                @click="download(0)"
              >下载</el-button>
              <el-button
                type="primary"
                @click="deletePayRecord(0)"
              >删除</el-button>
            </div>
          </div>
          <el-button size="small" type="primary" @click="sumbitUpload(0)">提交</el-button>
          <el-upload
            class="upload-demo"
            ref="upload"
            :action="returnAction()"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            multiple
            :on-exceed="handleExceed"
            :file-list="fileList"
            :auto-upload="false"
            :limit="1"
            :data="uploadData(0)"
          >
            <el-button
              size="small"
              type="primary"
            >点击上传</el-button>
          </el-upload>
        </el-tab-pane>
        <el-tab-pane label="增值税发票">
          <div class="recordInfo">合同编号：{{recordList.contractNo}}</div>
          <div class="recordInfo">交易客户：{{recordList.customerName}}</div>
          <div>
            <el-carousel
              height="350px"
              :autoplay="false"
              ref="payCarousel1"
            >
              <el-carousel-item
                v-for="item in recordList.fileTypeOne"
                :key="item.index"
              >
                <img
                  :src="'http://192.168.2.86:8087' + item.filePath"
                  class="bannerImg"
                  style="width: 880px; height:350px; max-width: 100%; max-height: 100%; text-align: center;"
                />
              </el-carousel-item>
            </el-carousel>
            <div class="buttonGroup">
              <el-button
                type="primary"
                @click="download(1)"
              >下载
              </el-button>
              <el-button
                type="primary"
                @click="deletePayRecord(1)"
              >删除</el-button>
            </div>
          </div>
          <el-button size="small" type="primary" @click="sumbitUpload(1)">提交</el-button>
          <el-upload
            class="upload-demo"
            ref="upload1"
            :action="returnAction()"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            multiple
            :on-exceed="handleExceed"
            :file-list="fileList"
            :auto-upload="false"
            :limit="1"
            :data="uploadData(1)"
          >
            <el-button
              size="small"
              type="primary"
            >点击上传</el-button>
          </el-upload>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
    <el-dialog
      title="保值方案"
      :visible.sync="methodVisible"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="保值方案">
          <div class="program">
            需提醒客户追加{{keepValuePlan.x>0 && isFinite(keepValuePlan.x)?keepValuePlan.x:"-"}}吨的货物或{{keepValuePlan.y>0?keepValuePlan.y:"-"}}元保证金。
          </div>
          <el-button
            type="primary"
            class="dialog-button"
            @click="methodVisible = false"
          >确定</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
    <el-dialog
      title="监控及影像信息"
      :visible.sync="monitorVisible"
      :close="monitorClosed"
    >
      <el-autocomplete
        v-model="state"
        :fetch-suggestions="querySearchAsync"
        placeholder="请输入内容"
        @select="handleSelect">
      </el-autocomplete>
      <el-tabs type="border-card">
        <el-tab-pane label="仓库">
          <table class="warehouseInfo">
            <tr>
              <th>仓库名称</th>
              <td>{{wareHouseInfo.warehouseName}}</td>
            </tr>
            <tr>
              <th>仓库地址</th>
              <td>{{wareHouseInfo.warehouseAddress}}</td>
            </tr>
            <tr>
              <th>联系人</th>
              <td>{{wareHouseInfo.linkman}}</td>
            </tr>
            <tr>
              <th>联系电话</th>
              <td>{{wareHouseInfo.linkPhone}}</td>
            </tr>
            <tr>
              <th>货物位置</th>
              <td>{{wareHouseInfo.goodsAddress}}</td>
            </tr>
            <tr>
              <th>货物真实性</th>
              <td>{{wareHouseInfo.goodsAuthenticity}}</td>
            </tr>
          </table>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";
import handleDialog from "../components/HandleDialog.vue";
import { rules } from "../validate.js";
import * as ajaxUrl from "../ajax.config.js";
import qs from "qs";

export default {
  data() {
    return {
      searchForm: {
        option: "",
        inputText: ""
      },
      searchParam: {},
      searchWarehouseId: "",

      programForm: {
        weight: "",
        ammount: ""
      },
      tableData: [],

      contractForm: {}, //合同信息
      wareHouseTable: [], //添加合同仓单信息
      addWareHouseTable: [], //追加仓单信息
      wareHouseInfo: {}, //仓单列表某条信息
      wareHouseInfoCurrent: {},
      wareHouseList: [], //仓单列表
      recordList: {
        fileTypeZero: [],
        fileTypeOne: []
      },
      //仓库信息
      warehouseForm: {},
      selectlistRow: [],
      selectlistRow1: [],

      currentPage: 1,
      pageSize: 10,
      totalPage: 1,

      dialogFormVisible: false,
      addContractVisible: false,
      warehouseVisible: false,
      warehouseInnerVisible: false,
      accountRecordVisible: false,
      monitorVisible: false,
      methodVisible: false,

      liquidated: false, //判断是否平仓

      form: {},
      formLabelWidth: "135px",
      pageType: 1,

      keepValuePlan: {},
      state: "",

      timeout: null,

      fileList: [],
      rules: rules,

      fileForm: {
        file: ''
      },
    };
  },
  mounted() {
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo(orderField, orderType) {
      this.searchParam[this.searchForm.option] = this.searchForm.inputText;
      this.axios
        .get(ajaxUrl.HOLDWARRANTLIST, {
          params: {
            pageSize: this.pageSize,
            pageNum: this.currentPage,
            search: this.searchParam,
            orderField: orderField,
            orderType: orderType
          }
        })
        .then(res => {
          this.tableData = res.data.resData;
          this.totalPage = res.data.page.total;
          this.pageSize = res.data.page.pageSize;
          this.currentPage = res.data.page.current;
          this.searchParam = {};
        })
        .catch(err => {
          console.log(err);
        });
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    //根据类型打开dialog
    openDialog(row, type) {
      if (type == "wareHouse") {
        //打开仓单面板
        this.warehouseVisible = true;
        if (row.warehouseWarrantList.length > 0) {
          this.wareHouseList = row.warehouseWarrantList;
          this.wareHouseInfo = row.warehouseWarrantList[0];
        }
          this.wareHouseInfoCurrent.customerName = row.customerName;
          this.wareHouseInfoCurrent.contractId = row.id;
      } else if (type == "operate") {
        //打开操作面板
        this.dialogFormVisible = true;
        this.addOperateStatus(row.id, 0);
        this.form = row;
        if (row.warehouseWarrantList.length > 1) {
          this.form.wareHouseId =
            row.warehouseWarrantList[0].wwNo +
            "~" +
            row.warehouseWarrantList[row.warehouseWarrantList.length - 1].wwNo;
        } else if ((row.warehouseWarrantList.length == 1)) {
          this.form.wareHouseId = row.warehouseWarrantList[0].wwNo;
        } else {
          this.form.wareHouseId = ("");
        }
      } else if (type == "contract") {
        //打开添加合同面板
        this.addContractVisible = true;
      } else if (type == "account") {
        //打开出账记录面板
        this.recordList = {
          fileTypeZero: [],
          fileTypeOne: []
        };
        this.accountRecordVisible = true;
        for (let i = 0; i < row.contractFileList.length; i++) {
          if (row.contractFileList[i].fileType == 0) {
            this.recordList.fileTypeZero.push(row.contractFileList[i]);
          }
          if (row.contractFileList[i].fileType == 1) {
            this.recordList.fileTypeOne.push(row.contractFileList[i]);
          }
        }
        this.recordList.contractNo = row.contractNo;
        this.recordList.customerName = row.customerName;
        this.recordList.id = row.id;
      } else if (type == "monitor") {
        this.monitorVisible = true;
        this.wareHouseInfo = {};
        this.state = "";
        this.wareHouseList = row.warehouseWarrantList;
      } else if (type == "method") {
        //打开保值方案面板
        this.methodVisible = true;
        this.keepValuePlan.x = row.originalPrice/row.goodsPrice - row.amount;
        this.keepValuePlan.y = row.originalPrice - row.amount*row.goodsPrice;
      }
    },
    dialogFormVisibleChange(val) {
      this.dialogFormVisible = val;
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.searchInfo();
    },
    //删除合同或仓单
    deleteRecord(type) {
      if (type == "合同") {
        this.$confirm("此操作删除该合同, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.axios
              .delete(ajaxUrl.HOLDWARRANTDELETE+"/"+ this.form.id)
              .then(res => {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.dialogFormVisible = false;
                this.$router.push("/GetContract");
                this.searchInfo();
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else if (type == "仓单") {
        this.$confirm("此操作删除该仓单, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.axios
              .delete(ajaxUrl.GETWAREHOUSEWARRANT+"/"+this.wareHouseInfo.id)
              .then(res => {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.warehouseVisible = false;
                this.$router.push("/GetContract");
                this.wareHouseInfo = {},
                this.searchInfo();
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
    //仓单状态处理
    statusSwitch(row, column) {
      switch (row.status) {
        case 0:
          return "存续";
          break;

        case 1:
          return "平仓";
          break;

        case 2:
          return "归档";
          break;
      }
    },
    //价格格式转换
    priceFormatter(row, column) {
      return row.originalPrice + " / " + row.amount * row.goodsPrice;
    },
    //仓单显示格式转换
    wwIdFormatter(row, column) {
      if (row.warehouseWarrantList.length == 0) {
        return "————";
      } else if (row.warehouseWarrantList.length == 1) {
        return row.warehouseWarrantList[0].wwNo;
      } else if (row.warehouseWarrantList.length > 1) {
        return (
          row.warehouseWarrantList[0].wwNo +
          "~" +
          row.warehouseWarrantList[row.warehouseWarrantList.length - 1].wwNo +
          "(" +
          row.warehouseWarrantList.length +
          ")"
        );
      }
    },
    pledgeRateFormatter(row) {
      let num = new Number(row.totalLoan/(100 * row.amount + row.deposit));
      return (num.toFixed(2)+"%")
    },
    //时间格式转换
    dateFormat(data) {
      return this.common.fullTime(data);
    },
    dateFormat2(row, column) {
      return this.common.fullTime(row.loanEndtime);
    },
    // 获取表格选中时的数据
    selectRow(val) {
      this.selectlistRow = val;
    },
    // 获取表格选中时的数据
    selectRow1(val) {
      this.selectlistRow1 = val;
    },
    // 增加行
    addRow() {
      let list = {
        rowNum: this.wareHouseTable.length + 1,
        address: this.address,
        name: this.name,
        weather: this.weather,
        phone: this.phone,
        date: this.date,
        mdate: this.mdate,
        loveer: this.loveer
      };
      this.wareHouseTable.unshift(list);
    },
    // 增加行
    addRow1() {
      let list = {
        rowNum: this.addWareHouseTable.length + 1,
        address: this.address,
        name: this.name,
        weather: this.weather,
        phone: this.phone,
        date: this.date,
        mdate: this.mdate,
        loveer: this.loveer
      };
      this.addWareHouseTable.unshift(list);
    },
    // 删除方法
    // 删除选中行
    delData() {
      for (let i = 0; i < this.selectlistRow.length; i++) {
        let val = this.selectlistRow;
        // 获取选中行的索引的方法
        // 遍历表格中tableData数据和选中的val数据，比较它们的rowNum,相等则输出选中行的索引
        // rowNum的作用主要是为了让每一行有一个唯一的数据，方便比较，可以根据个人的开发需求从后台传入特定的数据
        val.forEach((val, index) => {
          this.wareHouseTable.forEach((v, i) => {
            if (val.rowNum === v.rowNum) {
              // i 为选中的索引
              this.wareHouseTable.splice(i, 1);
            }
          });
        });
      }
      // 删除完数据之后清除勾选框
      this.$refs.wareHouseTable.clearSelection();
    },
    // 删除方法
    // 删除选中行
    delData1() {
      for (let i = 0; i < this.selectlistRow1.length; i++) {
        let val = this.selectlistRow1;
        // 获取选中行的索引的方法
        // 遍历表格中tableData数据和选中的val数据，比较它们的rowNum,相等则输出选中行的索引
        // rowNum的作用主要是为了让每一行有一个唯一的数据，方便比较，可以根据个人的开发需求从后台传入特定的数据
        val.forEach((val, index) => {
          this.addWareHouseTable.forEach((v, i) => {
            if (val.rowNum === v.rowNum) {
              // i 为选中的索引
              this.addWareHouseTable.splice(i, 1);
            }
          });
        });
      }
      // 删除完数据之后清除勾选框
      this.$refs.addWareHouseTable.clearSelection();
    },
    //远程搜索 下拉搜索功能
    querySearchAsync(queryString, cb) {
      let wareHouseList = this.wareHouseList;
      for (let i of wareHouseList) {
        i.value = i.wwNo; //将想要展示的数据作为value
      }
      let results = queryString
        ? wareHouseList.filter(this.createStateFilter(queryString))
        : wareHouseList;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 300 * Math.random());
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    handleSelect(item) {
      this.wareHouseInfo = item;
    },
    loadWarehouseId() {
      for (let i of this.wareHouseList) {
        i.value = i.wwNo; //将想要展示的数据作为value
      }
    },
    monitorClosed(){
      this.wareHouseList = [];
    },

    //上传功能
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },

    //删除轮播图
    deletePayRecord(fileType) {
      if (fileType == 0) {
        this.$confirm("此操作删除该回单, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.axios
              .delete(ajaxUrl.DELETEIMAGE + '/' + this.recordList.fileTypeZero[this.$refs.payCarousel.activeIndex].id)
              .then(res => {
                this.$message({
                  message: res.data.resMsg,
                  type: "success"
                });
                this.searchInfo();
                this.accountRecordVisible = false;
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else if (fileType == 1) {
        this.$confirm("此操作删除该发票, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.axios
              .delete(ajaxUrl.DELETEIMAGE + '/' + this.recordList.fileTypeOne[this.$refs.payCarousel.activeIndex].id)
              .then(res => {
                this.$message({
                  message: res.data.resMsg,
                  type: "success"
                });
                this.searchInfo();
                this.accountRecordVisible = false;
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
    //添加合同
    addContract() {
      this.$refs.contractForm.validate(valid => {
        if (valid) {
          let params = this.contractForm;
          params.warehouseWarrantList = this.wareHouseTable;
          this.axios
            .post(ajaxUrl.HOLDWARRANTADD, params)
            .then(res => {
              this.$message({
                message: "添加成功",
                type: "success"
              });
              this.addContractVisible = false;
              this.contractForm = {};
              this.wareHouseTable = [];
              this.searchInfo();
              this.$router.push("/GetContract");
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    download(fileType) {
      let url = ajaxUrl.DOWNLOADZIP + "?contractId=" + this.recordList.id + "&fileType=" + fileType
      this.axios({
        method: 'get',
        url: url,
        responseType:'blob',
      })
        .then((data) => {
          if(!data) {
            return;
          }
          let url = window.URL.createObjectURL(data.data)
          let link = document.createElement('a')
          link.style.display = 'none'
          link.href = url
          link.setAttribute('download', 'image.zip')
          document.body.appendChild(link)
          link.click()
        })
        .catch(err => {
          console.log(err);
        });
    },
    // fileChange(file, fileList){
    //   var This = this;
    //   var reader = new FileReader();
    //   reader.readAsDataURL(file.raw);
    //   reader.onload = function(e){ 
    //     this.result // 这个就是base64编码了
    //     This.fileForm.file = this.result;
    //   }
    //   console.log(this.fileForm)
    // },
    returnAction(){
      return ajaxUrl.UPLOADIMAGE;
    },
    uploadData(type){
      let params = {
        contractId: this.recordList.id,
        fileType: type
      }
      return params;
    },
    sumbitUpload(type){
      if(type == 0){
        this.$refs.upload.submit();
      }else{
        this.$refs.upload1.submit();
      }
    },
    //排序
    sortChange(params) {
      if(params.order == 'ascending'){
        this.searchInfo(params.prop, 'asc')
      }else if(params.order == 'descending'){
        this.searchInfo(params.prop, 'desc')
      }else{
        this.searchInfo()
      }
    },
    submitWarehouse(){
      let params = {
        id: this.wareHouseInfoCurrent.contractId,
        warehouseWarrantList: this.addWareHouseTable
      };
      console.log(this.wareHouseInfo.contractId)
      this.axios.post(ajaxUrl.BATCHWAREHOUSEWARRANT, params)
      .then(res => {
        this.warehouseInnerVisible = false;
        this.warehouseVisible = false;
        this.searchInfo();
        this.addWareHouseTable = {};
      }).catch(err => {
        console.log(err);
      })
    },
    uploadSuccess(response, file, fileList){
      this.$message({
        message: '上传成功',
        type: 'success'
      })
      this.accountRecordVisible = false;
      this.fileList = [];
      this.searchInfo();
    },
    uploadError(){
      this.$message.error('上传失败');
    },
    addOperateStatus(formId, formType){
      let params = {
        formId: formId,
        formType: formType
      };
      this.axios.post(ajaxUrl.INDEXINFO, params)
      .then(res => {
        console.log("success")
      }).catch(err => {
        console.log(err);
      })
    }
  },
  components: {
    vSearch,
    handleDialog
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.dialog-title {
  font-size: 20px;
  margin-bottom: 20px;
  display: block;
}
.buttonGroup {
  text-align: center;
  margin-left: -50px;
  margin-top: 25px;
}
.warehouseNum {
  float: left;
}
.head-tool {
  position: absolute;
  top: 24px;
  left: 105px;
}
.delete-warehouse {
  margin-bottom: 15px;
  margin-left: 15px;
  width: 84px;
  float: left;
}
.delete-warehouse:hover {
  cursor: pointer;
  border-bottom: 1px solid black;
}
.selectSearch {
  float: left;
  margin-left: 15px;
  margin-top: -5px;
}
.addWarehouse {
  margin-left: 15px;
  margin-top: -5px;
}
.dialog-info {
  font-size: 16px;
  margin-bottom: 20px;
}
.dialog-name {
  color: blue;
}
.recordInfo {
  margin-bottom: 10px;
  font-size: 16px;
}
.el-upload--text {
  width: 80px;
  height: 32px;
  border: 0px;
  margin-top: 5px;
}
.warehouseInfo {
  border-spacing: 0;
  border-collapse: collapse;
}
.warehouseInfo tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.warehouseInfo tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
</style>



